import React from 'react'
import ReactDOM from 'react-dom'
import { Carousel } from "react-bootstrap";
import { useState } from 'react';

function State() {
    const [ index, setIndex ] = useState(0); 

    const handleSelect = (selectedIndex, e) => {
      setIndex(selectedIndex);  
      e.preventDefault();  
    };

    return (
      <div>
      <Carousel className="slider" interval={null} activeIndex={index} onSelect={handleSelect}> 
        <Carousel.Item> 
            <h1 className="inslider"
            > IN OFFICE
            </h1>
        </Carousel.Item>
        <Carousel.Item>
        <h1 
        width="57%"
        className="inslider"
            > VACATION
            </h1>
        </Carousel.Item>
        <Carousel.Item>
        <h1 className="inslider2"
            >WORKING REMOTELY
            </h1>
        </Carousel.Item>
        <Carousel.Item>
        <h1 className="inslider"
            >LUNCH
            </h1>
        </Carousel.Item>
        <Carousel.Item>
        <h1 className="inslider"
            >IN MEETING
            </h1>
        </Carousel.Item>
      </Carousel>
      </div>
    );
  }

  export default State;
